@import url(https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap);
*{
  margin:0;
}


body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.headerContainer{
    display: flex;
    flex-direction: column;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 300;
}

.header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 10px;
    padding-bottom: 10px;
}

.collapseIcon{
    color: white;
    display: block;
}

.header__logo{
    object-fit: contain;
}

.header__title{
    display: none;
    color: white;
    margin-left: 10px;
    margin-right: 10px;
}

@media(min-width:800px){
    .collapseIcon{display: none;}
    .header__title{display: block;}
}

.sidebarRow{
    display:flex;
    align-items: center;
    padding: 10px 15px;
    margin-bottom: 9px;
    border-radius: 5px;
}
.sidebarRow__title{
    flex:1 1;
    margin-left: 10px;
    font-size: 13px;
    font-weight: 400;
}
.sidebarRow:hover{
    cursor: pointer;
    background-color: #f8f8f8;
}
.sidebarRow:hover > .sidebarRow__icon{
    color: red;
}

.colRow{
    margin-left: 10px;
    margin-right: 10px;
    display:flex;
    align-items: center;
    padding: 10px 10px;
    margin-bottom: 9px;
    border-radius: 5px;
}
.colRow:hover{
    cursor: pointer;
    background-color: #29037a;
}
.colRow:hover > .sidebarRow__icon{
    color: red;
}


@media (min-width: 1000px) {
.sidebarRow{margin-left: 15px; margin-right: 15px;}
}
.sidebar1{
    width: 100%;
    z-index:200;
}

.SidebarRow__container{
    padding-top: 10px;
    height:100%;
}

.searchBar{
    margin-left: 10px;
    margin-right: 10px;
    margin-bottom: 10px;
    display:flex;
    align-items: center;
    border:1px solid;
}

.searchButton{
    width:50px !important;
}

.searchBar > input{
    padding: 10px;
    flex:1 1;
    border: none;
}


hr{
    height: 1px;
    border:0;
    margin-top:10px;
    margin-bottom: 10px;
}

.sidebar{
    flex:0.15 1;
    padding-top: 20px;
}

.sidebar >hr{
    height: 1px;
    border:0;
    margin-top:10px;
    margin-bottom: 10px;
}
.app__page{
    font-family: Inter;
    display:flex;
    flex:1 1
}

.app{
    display: flex;
    height: 100vh;
    flex-direction: column;
}

.presetFrame{
    border:none;
    height: 70px;
    width: 80px;
    margin-bottom: 10px;
    margin-right: 10px;
    margin-top: 10px;
}

.presetFrame:hover{
    cursor: pointer;
    border:2px solid tan;
    background-color: tan;
}

.presetSvg{
    height: auto;
    width: auto;
}


.createBadge__tool{
    height: 40px;
    width: 40px;
    display:flex;
    margin: auto;
}

.createBadge__tool__star{
    height: 30px;
    display:flex;
    margin: auto;
    width: 30px;
}


.frame{
    border:none;
    height: 50px;
    width: 60px;
    margin-bottom: 10px;
    margin-top: 10px;
}

.frame:hover{
    border:2px solid tan;
    cursor: pointer;
    background-color: tan;
}

.frame:focus{
    border:none;
}

.frame.selected{
    cursor: pointer;
    border:2px solid tan;
    height: 50px;
    width: 60px;
    margin-bottom: 10px;
    margin-top: 10px;
}

.createBadge__model{
    height: 60px;
    width: 60px;
    display: flex;
    margin: auto;
}

.createBadge__model__IconModels{
    height: 55px;
    width: 55px;
    display: flex;
    margin: auto;
}

.modelFrame{
    
    cursor: pointer;
    border:none;
    background-color: transparent;
    height: 70px;
    width: 80px;
    margin-bottom: 10px;
    margin-top: 10px;
}

.modelFrame:hover{
    border:2px solid tan;
    cursor: pointer;
}

.modelFrame:focus{
    border:none;
}

.searchBar2{
    margin-top: 10px;
    margin-bottom: 10px;
    border:1px solid;
}


.input{
    padding: 10px;
    flex:1 1;
    border:none;
}

.createBadgePage{
