    margin-right: 20px;
}

.presetTab >hr{
    height: 1px;
    border:0;
    margin-top:10px;
    margin-bottom: 10px;
}

.create{
    flex: 1 1;
    padding-top: 0px;
    padding-bottom: 20px;
    padding-right: 20px;
    padding-left: 20px;
}


.presetTab__mobileView{
    margin-left: -20px;
    margin-right: -20px;
    padding-top: 20px;
    padding-bottom: 20px;
}

.presetLabel__mobileView{
    display: flex;
    margin-left: 20px;
    margin-right: 20px;
}


.presets__mobileView{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    padding-top:10px;
    margin-left: 20px;
    margin-right: 20px;
}

.canvas{
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
    margin-top: 20px;
    height: auto;
    padding: 50px;
    padding-left:90px;
    padding-right:90px;
    border-radius: 10px;
    /* box-shadow: 2px 2px 10px 2px #ededed; */
}

