    padding: 10px;
    border:1px solid;
}

.description{
    margin-top:10px;
    flex:0.5 1;
    border-radius: 10px;
    padding: 10px;
    border:1px solid;
}

.createButton{
    cursor: pointer;
    margin-top: 20px;
    border:none;
    color:white;
    font-size: medium;
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 10px;
    padding-bottom: 10px;
}

.draftButton{
    margin-top: 20px;
    cursor: pointer;
    border:none;
    background-color: transparent;
    color:#5200ff;
    font-size: medium;
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 10px;
    padding-bottom: 10px;
}

.models{
    display: none;
    padding-top: 20px;
    flex:0.15 1;
    background-color: red;
    padding-bottom: 20px;
    margin-bottom:20px;
}

.models__mobileView{
    padding-top: 20px;
    flex:0.15 1;
    padding-bottom: 20px;
    margin-bottom:20px;
}

.modelsContainer{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: space-evenly;
    margin-left: 10px;
    margin-right: 10px;
}

.modelsContainer__mobileView{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin-left: -20px;
    margin-right: -20px;
    margin-bottom: 20px;
    padding-right: 10px;
    padding-left: 10px;
    padding-top: 10px;
    padding-bottom: 20px;
}

.tools{
    padding-top: 20px;
    display: none;
    flex-direction: column;
    flex:0.05 1;
    align-items: center;
}

.tools__mobileView{
    margin-left: -20px;
    margin-right: -20px;
    padding-right: 10px;
    padding-left: 10px;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
}

@media (min-width: 800px) {
.presetTab{display: flex;}
.presetTab__mobileView{display: none;}
.create{flex: 0.8 1;}
.tools__mobileView{display: none;}
.models__mobileView{display: none;}
.models{display: block;}
.tools{display: flex;}
.svgClass{width: 322px; height: 322px;}
.badgeName{flex: 0.5 1}
}
.createdBadges{
    padding-top: 25px;
    padding-left: 20px;
    padding-right: 20px;
    flex:1 1;
}

.heading{
    flex: 1 1;
    margin: 0 auto;
    display: grid;
    grid-column-gap: 10px;
    grid-row-gap: 10px;
}

.searchBarContainer{
    display: flex;
    flex-wrap: wrap;
    border:1px solid;
}

.space{
    flex: 0 1;
}

.input1{
    flex: 1 1;
    padding: 10px;
    border: none;
}

.hr{
    height: 1px;
    border: 0;
    margin-top: 10px;
    margin-bottom: 20px;
}

.yearText{
    margin-top: 30px;
}

.badgeContainer{
    margin: 0 auto;
    display: grid;
    grid-column-gap: 20px;
    grid-row-gap: 20px;
}

.badge{
    width: 90%;
    border-radius: 5px;
    border: 1px solid;
    padding-left: 10px;
    padding-right: 10px;
    padding-bottom: 20px;
}

.badgeNameContainer{
    margin-left:10px;
    margin-right:10px;
    margin-top:20px;
    margin-bottom:20px;
    display: flex;
}

.badgeNameText{
    font-size: 12px;
}

.createdBadgeSvg{
    height: auto;
    width: auto;
}

@media (min-width: 100px) {
.badgeContainer { grid-template-columns: repeat(2, 1fr);}
}
@media (min-width: 600px) {
.badgeContainer { grid-template-columns: repeat(3, 1fr);}
}
@media (min-width: 800px) {
.createdBadges{flex: 0.85 1}
.space{flex: 0.5 1}
.badgeContainer { grid-template-columns: repeat(4, 1fr);}
.heading { grid-template-columns: repeat(2, 1fr);}
}
@media (min-width: 1000px) {
.badgeContainer { grid-template-columns: repeat(5, 1fr);}
.heading { grid-template-columns: repeat(2, 1fr);}
}
@media (min-width: 1200px) {
.badgeContainer { grid-template-columns: repeat(6, 1fr);}
.heading { grid-template-columns: repeat(2, 1fr);}
}
@media (min-width: 1400px) {
.badgeContainer { grid-template-columns: repeat(7, 1fr);}
.heading { grid-template-columns: repeat(2, 1fr);}
}
